.tab-container
{   
    width: fit-content;
    display: flex;
    background: var(--primary);
    border-radius: 40px 40px 0px 0px;


    .tab
    {
        padding: 16px 50px;
        display: flex;
        align-items: center;
        text-align: center;
        background: var(--primary);
        border-radius: 40px 40px 0px 0px;
        font-style: normal;
        font-weight: 600;
        font-size: 2.6rem;
        line-height: 1.3em;
        color: #FFFFFF;
        cursor: pointer;
        transition: .25s;
        &:hover
        {
            color: var(--secondary);
        }
        &.active
        {
            background: #FFFFFF;
            color: var(--primary);
            position: relative;
            // &::after
            // {
            //     content: '';
            //     background: #FFFFFF;
            //     border-radius: 40px 40px 0px 0px;
            //     position: absolute;
            // }
        }
    }
}


.progress-videos__container
{
    height: 600px;
    width: 100%;
    position: relative;
    .progress-videos-item
    {   
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: #000000;
        border-radius: 0px 40px 40px 40px;
        cursor: pointer;
        &::before
        {
            content: '';
            width: 100px;
            height: 100px;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translateX(-50%) translateY(-50%);
            background: url('../img/play.svg') no-repeat center/contain;
        }
        video
        {
            width: 100%;
            height: 100%;
            
        }
    }
}

@media (max-width:991px) {
    .progress-videos__container
    {
        height: 400px;
    }
}

@media (max-width:576px) {
    .tab-container
    {
        width: 100%;
        .tab
        {
            padding: 10px 22px;
            font-size: 16px;
            flex-grow: 1;
            justify-content: center;
        }
    }

    .progress-videos__container
    {
        height: 200px;
        .progress-videos-item
        {
            border-radius: 0 0 40px 40px;
            &::before
            {
                width: 50px;
                height: 50px;
            }
        }
    }
}